<template>
    <nav class="side-bar">
        <div>
            <div class="search">
                <input type="text" placeholder="搜索" v-model="query">
            </div>
            <h1>Vue-Plugin</h1>
            <ul>
                <li class="nav-item active" v-for="(item, index) in searchResult" :key="index">
                    <router-link class="nav-link" :to="`/${index}`" >{{index}}</router-link>
                </li>
            </ul>
        </div>
    </nav>
</template>

<script>
    export default {
        name: "SideNav",
        props: {
            data: Object
        },
        data() {
            return {
                query: '',   // 搜索参数
                searchResult: {}    // 搜索结果
            }
        },
        watch: {
            // 初始化搜索结果
            data() {
                this.searchResult = this.data;
            },
            // 根据搜索参数显示结果
            query(searchValue) {
                if (!searchValue) {
                    this.searchResult = this.data;
                    return;
                }
                this.searchResult = {};
                Object.keys(this.data).forEach(val => {
                    if (val.indexOf(searchValue.toLowerCase()) !== -1) {
                        this.searchResult[val] = this.data[val];
                    }
                })
            }
        }
    }
</script>

