import Vue from 'vue'
import Router from 'vue-router'
import components from '../components'

let routerMap = [];
// 循环添加路由
Object.keys(components).forEach((file) => {
    routerMap.push({
        path: '/' + file,
        name: file,
        component: () => import (`../packages/${file}/example/App.vue`)
    })
})

Vue.use(Router)

export default new Router({
    mode: 'history',
    base: process.env.BASE_URL,
    routes: routerMap
})
